"use strict";

/**
 *
 * @author xgqfrms 2018.04.13
 * @license MIT
 * @copyright xgqfrms
 *
 * @description autoChangeSkin
 * @augments
 * @example autoChangeSkin(`html`, colors, skin);
 *
 * @param {String} uid
 * @param {Array} colors
 * @param {String} skin
 * @param {Boolean} debug
 *
 */


const autoChangeSkin = (colors = [], skin = `white`, debug = false) => {
    const html = document.querySelector(`html`);
    colors.forEach(
        (color, i) => {
            if (color) {
                let newColor = `var(${color}-${skin})`;
                html.style.setProperty(color, newColor);
            }
        }
    );
};


const changeSkin = (uid = `html`, colors = [], skin = `default`, debug = false) => {
    const html = document.querySelector(`html`);
    colors.forEach(
        (color, i) => {
            if (color) {
                let newColor = `var(${color}-${skin})`;
                html.style.setProperty(color, newColor);
            }
        }
    );
};


export default autoChangeSkin;

export {
    autoChangeSkin,
    changeSkin
};



