// polyfill
import "babel-polyfill";

// css
import "../css/index.css";
// utils
// import { FetchIP } from "../utils/urls";
// import { fetchJSON, getDatas } from "../utils/fetch-ajax";
import { supportImport, HTMLGenerator } from "./template-loader";
import { SearchParams } from "../utils/url-serach-parmas";
import { autoChangeSkin } from "../utils/auto-change-skin";
import { $qs } from "../utils/no-need-jquery";



// js !== json ? ES6 import json === OK

// import * as CC from "./colors.json";
// const COLORS = require("./colors.json");
// import { colors } from "./colors";


// console.log(`colors =`, JSON.stringify(CC, null, 4));
// console.log(`colors =`, JSON.stringify(colors, null, 4));
// console.log(`COLORS.colors =`, JSON.stringify(COLORS.colors, null, 4));

// const fetchIP = FetchIP;



if (supportImport) {
    console.log(`YES! The browser supports the HTML imports template!`);
    // setTimeout(() => {
    //     HTMLGenerator(`module_001`);
    // }, 0);
    // setTimeout(() => {
    //     HTMLGenerator(`module_002`);
    // }, 0);
    setTimeout(() => {
        HTMLGenerator(`latest-news`);
        const colors = [
            // "--body-color",
            "--body-background",
            "--title-box-border",
            "--title-box-background",
            "--title-icons-color",
            "--title-icons-background",
            "--table-color",
            "--table-background",
            "--table-td-color",
            "--table-td-border",
            "--fixed-width-color"
            // "",
        ];
        let search = decodeURIComponent(window.location.search);
        let skin = SearchParams(search).skin || "white";
        // console.log(`SearchParams(search).skin = `, SearchParams(search).skin);
        // console.log(`SearchParams().skin = `, SearchParams().skin);
        // if (skin !== "white") {
        //     autoChangeSkin(colors, skin);
        // } else {
        //     autoChangeSkin(colors);
        //     console.log(`default skin = white`, skin);
        // }
        let skinBtn = $qs(`[data-action="change-skin"]`);
        skinBtn.addEventListener(`click`, () => {
            if (skin === "white") {
                skin = "black";
                // console.log(`UI skin is changing to black!`);
            } else {
                skin = "white";
                // console.log(`UI skin is changing to white!`);
            }
            autoChangeSkin(colors, skin);
        });
    }, 0);
} else {
    console.error(`Oops! The browser supports the HTML imports template!`);
}



