"use strict";

/**
 *
 * @author xgqfrms
 * @license MIT
 * @copyright gildata
 *
 * @description SearchParamsGenerator
 * @augments
 * @example
 *
 * @param {String} search
 * @param {Boolean} debug
 *
 */


const SearchParams = (search = ``, debug = false) => {
    let params = "";
    if (search) {
        params = new URLSearchParams(search);
    } else {
        // default search
        search = decodeURIComponent(window.location.search);
        params = new URLSearchParams(search);
    }
    let result = {};
    for (let param of params) {
        let key = ``,
            value = ``;
        key = param[0];
        value = param[1];
        result[key] = value;
    }
    return result;
};


const SearchParamsGenerator = (search = ``, debug = false) => {
    const params = new URLSearchParams(search);
    let result = {},
        entries = [...params.entries()],
        keys = [...params.keys()],
        values = [...params.values()];
    for (let param of params) {
        let key = ``,
            value = ``;
        key = param[0];
        value = param[1];
        result[key] = value;
    }
    return {
        result,
        entries,
        keys,
        values
    };
};


export default SearchParams;
export {
    SearchParams,
    SearchParamsGenerator
};


/*

let search = decodeURIComponent(window.location.search);
// "?gilcode=600570.SH&skin=white&name=&type=0"


SearchParams(search);
SearchParams(search).gilcode;
// "600570.SH"
SearchParams(search).skin;


SearchParamsGenerator(search);
SearchParamsGenerator(search).result.gilcode;
// "600570.SH"


*/



